import { mapMutations } from 'vuex'
export default {
	data() {
		return {
			nationCode: '86',
			mobile: '',
			password: '',
			verificationCode : '',
			codeButtonText: '发送',
			timer: null,
			seconds: 60,
			isTimeDown: false,
			isLoginByPassword: true,
			codeButtonDisabled: true,
			loginButtonDisabled: true,
			changeLoginText: '验证码登录'
		}
	},
	methods: {
		...mapMutations(['login']),
		changeLoginClick() {
			this.isLoginByPassword = !this.isLoginByPassword
			this.changeLoginText = this.isLoginByPassword ? '验证码登录' : '密码登录'
		},
		nationCodeClick() {
			uni.showToast({
				icon: 'none',
				title:'当前只支持中国手机号',
				duration:2000
			})
		},
		mobileInput(event) {
			// https://blog.csdn.net/weixin_43343144/article/details/101097177
			setTimeout(() => {
				const value = event.detail.value
				this.mobile = value
				if (this.nationCode == '86') {
					if (value.length == 11) {
						this.codeButtonDisabled = false
					} else {
						this.codeButtonDisabled = true
					}
				} else {
					if (value.length >= 4 && value.length <= 15) {
						this.codeButtonDisabled = false
					} else {
						this.codeButtonDisabled = true
					}
				}
				this.loginButtonAbled()
			}, 0)
		},
		passwordInput(event) {
			setTimeout(() => {
				this.password = event.detail.value
				this.loginButtonAbled()
			}, 0)
		},
		verificationCodeInput(event) {
			setTimeout(() => {
				this.verificationCode = event.detail.value
				this.loginButtonAbled()
			}, 0)
		},
		loginButtonAbled() {
			if (this.isLoginByPassword) {
				if (this.password.length >= 6 && this.password.length <= 18 && !this.codeButtonDisabled) {
					this.loginButtonDisabled = false
				} else {
					this.loginButtonDisabled = true
				}
			} else {
				if (this.verificationCode.length == 4 && !this.codeButtonDisabled) {
					this.loginButtonDisabled = false
				} else {
					this.loginButtonDisabled = true
				}
			}
		},
		codeButtonClick() {
			if (this.isTimeDown || this.codeButtonDisabled) {
				return;
			}
			uni.showLoading({
				title: '',
				mask: true
			})
			const param = {
				mobile: this.mobile,
				nationCode: this.nationCode,
				typeC: 'login'
			}
			uni.request({
				url: 'https://gateway.stey.com/stey-service/security/v2/vercode',
				method: 'POST',
				data: {
					data: param,
					timestamp: Date.now()
				},
				header: {
					'Content-Type': 'application/json; charset=utf-8'
				},
				responseType: '',
				success: res => {
					if (res.statusCode == 200) {
						this.isTimeDown = true;
						this.countDown();
						this.timer = setInterval(() => {
							this.seconds--
							if (this.seconds <= 0) {
								clearInterval(this.timer)
								this.isTimeDown = false;
								this.codeButtonText = '发送'
								return
							}
							this.countDown()
						}, 1000)
					} else {
						const error = res.data.error
						if (error) {
							uni.showToast({
								icon: 'none',
								title: error,
								duration: 2000
							});
						}
					}
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading()
				}
			});
		},
		countDown() {
			this.codeButtonText = this.seconds + ' s'
		},
		loginButtonClick() {
			uni.showLoading({
				title: '',
				mask: true
			});
			const providerId = this.isLoginByPassword ? 'mobile' : 'token'
			const passwordOrToken = this.isLoginByPassword ? this.password : this.verificationCode
			const param = {
				providerKey: `${this.nationCode}-${this.mobile}`,
				providerId,
				passwordOrToken,
				accessPortal: 'mobile_app'
			}
			uni.request({
				url: 'https://gateway.stey.com/auth-service/auth/login',
				method: 'POST',
				data: {
					data: param,
					timestamp: Date.now()
				},
				header: {
					'Content-Type': 'application/json; charset=utf-8'
				},
				responseType: '',
				success: res => {
					console.log(res)
					if (res.statusCode == 200) {
						const token = res.data.data.accessToken
						uni.setStorageSync('token', token)
						uni.setStorageSync('haveLogin', true)
						// 提交
						this.login()
						uni.showToast({
							title: '登录成功',
							duration: 2000
						});
						uni.navigateBack()
					} else {
						const error = res.data.error
						if (error) {
							uni.showToast({
								icon: 'none',
								title: error,
								duration: 2000
							});
						}
					}
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading()
				}
			});
		}
	},
	beforeDestroy() {
		clearInterval(this.timer)
	}
}
